
<template>
  <view class="uni-container">
    <uni-forms ref="form" :value="formData" validate-trigger="submit" err-show-type="toast">
      <uni-forms-item name="type" label="广播类型">
  <uni-easyinput placeholder="广播类型" v-model="formData.type" trim="both" />
</uni-forms-item>
<uni-forms-item name="title" label="广播标题">
  <uni-easyinput placeholder="广播标题" v-model="formData.title" trim="both" />
</uni-forms-item>
<uni-forms-item name="content" label="广播内容">
  <uni-easyinput placeholder="广播内容" v-model="formData.content" trim="both" />
</uni-forms-item>
<uni-forms-item name="form_item" label="填表内容">
  <uni-data-checkbox :multiple="true" v-model="formData.form_item" />
</uni-forms-item>
<uni-forms-item name="publisher_id" label="发布者ID">
  <uni-easyinput placeholder="发布者ID" v-model="formData.publisher_id" trim="both" />
</uni-forms-item>
<uni-forms-item name="class_id" label="班级ID">
  <uni-data-checkbox :multiple="true" v-model="formData.class_id" />
</uni-forms-item>
<uni-forms-item name="release_time" label="">
  <uni-datetime-picker return-type="timestamp" :value="formData.release_time" />
</uni-forms-item>
<uni-forms-item name="confirmed" label="确认成员">
  <uni-data-checkbox :multiple="true" v-model="formData.confirmed" />
</uni-forms-item>

      <view class="uni-button-group">
        <button type="primary" class="uni-button" @click="submit">提交</button>
      </view>
    </uni-forms>
  </view>
</template>

<script>
  import { validator } from '../../js_sdk/validator/class-notice.js';

  const db = uniCloud.database();
  const dbCollectionName = 'class-notice';

  function getValidator(fields) {
    let reuslt = {}
    for (let key in validator) {
      if (fields.indexOf(key) > -1) {
        reuslt[key] = validator[key]
      }
    }
    return reuslt
  }

  export default {
    data() {
      return {
        formData: {
  "type": "",
  "title": "",
  "content": "",
  "form_item": [],
  "publisher_id": "",
  "class_id": [],
  "release_time": null,
  "confirmed": []
},
        formOptions: {},
        rules: {
          ...getValidator(["type","title","content","form_item","publisher_id","class_id","confirmed","release_time"])
        }
      }
    },
    onLoad(e) {
      const id = e.id
      this.formDataId = id
      this.getDetail(id)
    },
    onReady() {
      this.$refs.form.setRules(this.rules)
    },
    methods: {
      /**
       * 触发表单提交
       */
      submit() {
        uni.showLoading({
          mask: true
        })
        this.$refs.form.submit().then((res) => {
          this.submitForm(res)
        }).catch((errors) => {
          uni.hideLoading()
        })
      },

      submitForm(value) {
        // 使用 clientDB 提交数据
        db.collection(dbCollectionName).doc(this.formDataId).update(value).then((res) => {
          uni.showToast({
            icon: 'none',
            title: '修改成功'
          })
          this.getOpenerEventChannel().emit('refreshData')
          setTimeout(() => uni.navigateBack(), 500)
        }).catch((err) => {
          uni.showModal({
            content: err.message || '请求服务失败',
            showCancel: false
          })
        }).finally(() => {
          uni.hideLoading()
        })
      },

      /**
       * 获取表单数据
       * @param {Object} id
       */
      getDetail(id) {
        uni.showLoading({
          mask: true
        })
        db.collection(dbCollectionName).doc(id).field('type,title,content,form_item,publisher_id,class_id,confirmed').get().then((res) => {
          const data = res.result.data[0]
          if (data) {
            this.formData = data
          }
        }).catch((err) => {
          uni.showModal({
            content: err.message || '请求服务失败',
            showCancel: false
          })
        }).finally(() => {
          uni.hideLoading()
        })
      }
    }
  }
</script>

<style>
  .uni-container {
    padding: 15px;
  }

  .uni-input-border,
  .uni-textarea-border {
    width: 100%;
    font-size: 14px;
    color: #666;
    border: 1px #e5e5e5 solid;
    border-radius: 5px;
    box-sizing: border-box;
  }

  .uni-input-border {
    padding: 0 10px;
    height: 35px;

  }

  .uni-textarea-border {
    padding: 10px;
    height: 80px;
  }

  .uni-button-group {
    margin-top: 50px;
    display: flex;
    justify-content: center;
  }

  .uni-button {
    width: 184px;
    padding: 12px 20px;
    font-size: 14px;
    border-radius: 4px;
    line-height: 1;
    margin: 0;
  }
</style>
